}

body {
    background-color: #F0F8FF;
}

.main-container{
    display: flex;
    background-color: #F0F8FF;
    flex-direction: column;
}

.cards-container{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: #F0F8FF;
}

.cards-tolkien{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: #F0F8FF;
}

.cards-asimov{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: #f4eff8;
}

.joke-container{
    width: 100%;
    display: flex;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    justify-content: space-between;
    flex-direction: column;
    background-color: #b9e7ff;
}

.joke-text-container{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
    flex-direction: column;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    justify-content: center;
    margin: 16px;
    /*background-color: #cccccc;*/
}
.joke-text{
    font-size: 1.1em;
    white-space: pre-wrap;
    word-wrap: break-word;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.load-btn-container{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    /*background-color: #ffb687;*/
}

header {
    /*POWDER BLUEbackground-color: #b0e0e6;*/
    /* SKY BLUE::::background-color: #87CEEB;*/
    /* CORAL:::: #FF5733*/
    background-color: 	#4682B4;
    margin: auto -16px;
    padding: 16px 32px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
}

header h1 {
    color: #fff;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

footer {
    position: absolute;
    text-align: center;
    bottom: 0;
    width: 100%;
    height: 2.5rem;
}

footer p {
    color: #ccc;
}
.card {
    cursor: pointer;
    display: flex;
    flex-direction: row;
    background: #fff;
    border-radius: 7px;
    box-shadow: 0 2px 5px #ccc;
    padding: 8px;
    width: 275px;
    margin: 16px;
    float: left;
}

.card:hover{
    background: #4682B4;
}

.card h1 {
    font-size: 1.1em;
    margin-bottom: 6px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.card p {
    font-size: 1.1em;
    margin-bottom: 10px;
    white-space: pre-wrap;
    word-wrap: break-word;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.card-profile-picture {
    height: 50px;
    width: 50px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.card-profile-picture-container {

    /*background-color: #87CEEB;*/
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.card-profile-picture-info {
    /*background-color: #fdac98;*/
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-left: 10px;
}

